				<view class="login-title">Ê¨¢Ëøé‰ΩøÁî®Êâã‰∏≤ÂÆöÂà∂</view>
				<view class="login-desc">ËØ∑‰ΩøÁî®ÂæÆ‰ø°ÁôªÂΩï‰ª•ÁªßÁª≠</view>
				<button 
					class="wechat-login-btn" 
					:disabled="logging"
					open-type="getUserInfo"
					@getuserinfo="onGetUserInfo"
				>
					<text v-if="logging">ÁôªÂΩï‰∏≠...</text>
					<text v-else>ÂæÆ‰ø°Âø´ÈÄüÁôªÂΩï</text>
				</button>
			</view>
		</view>
		
		<!-- Â∑≤ÁôªÂΩïÁä∂ÊÄÅ -->
		<view v-else class="user-section">
			<!-- Áî®Êà∑‰ø°ÊÅØÂç°Áâá -->
