				<view class="menu-item" @click="goToWorkspace">
					<view class="menu-icon">üé®</view>
					<view class="menu-content">
						<view class="menu-title">Â∑•‰ΩúÂè∞</view>
						<view class="menu-desc">ËÆæËÆ°‰Ω†ÁöÑ‰∏ìÂ±ûÊâã‰∏≤</view>
					</view>
					<text class="menu-arrow">‚Ä∫</text>
				</view>
				
				<view class="menu-item" @click="goToPortfolio">
					<view class="menu-icon">‚ú®</view>
					<view class="menu-content">
						<view class="menu-title">ÊàëÁöÑ‰ΩúÂìÅÈõÜ</view>
						<view class="menu-desc">Êü•Áúã‰øùÂ≠òÁöÑËÆæËÆ°</view>
					</view>
					<text class="menu-arrow">‚Ä∫</text>
				</view>
				
				<view class="menu-item" @click="goToOrders">
					<view class="menu-icon">üì¶</view>
					<view class="menu-content">
						<view class="menu-title">ÊàëÁöÑËÆ¢Âçï</view>
						<view class="menu-desc">Êü•ÁúãËÆ¢Âçï‰∏éÁâ©ÊµÅÁä∂ÊÄÅ</view>
					</view>
					<text class="menu-arrow">‚Ä∫</text>
				</view>
				
				<view class="menu-item" @click="goToAddresses">
					<view class="menu-icon">üìç</view>
					<view class="menu-content">
						<view class="menu-title">Êî∂Ë¥ßÂú∞ÂùÄ</view>
						<view class="menu-desc">ÁÆ°ÁêÜÊî∂Ë¥ßÂú∞ÂùÄ</view>
					</view>
					<text class="menu-arrow">‚Ä∫</text>
				</view>
			</view>
			
			<!-- ÈÄÄÂá∫ÁôªÂΩï -->
			<view class="logout-section">
				<button class="logout-btn" @click="handleLogout">ÈÄÄÂá∫ÁôªÂΩï</button>
			</view>
		</view>
	</view>
</template>

<script>
	import { authAPI } from '@/utils/api.js'
	
	export default {
		data() {
			return {
				user: null,
				userInfo: {},
				logging: false
			}
		},
		computed: {
			userInitial() {
				if (!this.user) return 'U'
				const name = this.userInfo.nickName || this.user.name || 'ÂæÆ'
				return name[0].toUpperCase()
			}
		},
		onLoad() {
			this.checkLogin()
		},
		onShow() {
			this.checkLogin()
		},
		methods: {
			// ÈÄöËøábuttonÁöÑopen-typeËé∑ÂèñÁî®Êà∑‰ø°ÊÅØÔºàÂ∞èÁ®ãÂ∫èÊé®ËçêÊñπÂºèÔºâ
			onGetUserInfo(e) {
				console.log('ÈÄöËøábuttonËé∑ÂèñÁî®Êà∑‰ø°ÊÅØ:', e)
				let userInfo = {}
				if (e.detail && e.detail.userInfo) {
					// Áî®Êà∑ÂêåÊÑèÊéàÊùÉ
					userInfo = {
						nickName: e.detail.userInfo.nickName,
						avatarUrl: e.detail.userInfo.avatarUrl,
						gender: e.detail.userInfo.gender,
						country: e.detail.userInfo.country,
						province: e.detail.userInfo.province,
						city: e.detail.userInfo.city
					}
					console.log('Ëß£ÊûêÂêéÁöÑÁî®Êà∑‰ø°ÊÅØ:', userInfo)
					console.log('ÊòµÁß∞:', userInfo.nickName, 'Â§¥ÂÉè:', userInfo.avatarUrl)
					// ‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØÂà∞Êú¨Âú∞
					uni.setStorageSync('wechatUserInfo', userInfo)
					// Á´ãÂç≥Êõ¥Êñ∞userInfoÔºåÁ°Æ‰øùUIËÉΩÊòæÁ§∫
					this.userInfo = userInfo
					console.log('ËÆæÁΩÆÂêéÁöÑthis.userInfo:', this.userInfo)
				} else {
					// Áî®Êà∑ÊãíÁªùÊéàÊùÉ
					console.log('Áî®Êà∑ÊãíÁªùÊéàÊùÉ')
					uni.showToast({
						title: 'ÈúÄË¶ÅÊéàÊùÉÊâçËÉΩËé∑ÂèñÊòµÁß∞ÂíåÂ§¥ÂÉè',
						icon: 'none',
						duration: 2000
					})
				}
				// Êó†ËÆ∫ÊòØÂê¶ÊéàÊùÉÔºåÈÉΩÁªßÁª≠ÁôªÂΩïÊµÅÁ®ã
				this.handleWechatLoginWithInfo(userInfo)
			},
			
			async handleWechatLoginWithInfo(userInfo) {
				this.logging = true
				try {
					// 1. Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcode
					const loginRes = await new Promise((resolve, reject) => {
						uni.login({
							provider: 'weixin',
							success: resolve,
							fail: reject
						})
					})
					
					if (!loginRes.code) {
						throw new Error('Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïcodeÂ§±Ë¥•')
					}
					
					console.log('Ëé∑ÂèñÁôªÂΩïcodeÊàêÂäüÔºåÂºÄÂßãË∞ÉÁî®ÁôªÂΩïAPI...')
					console.log('‰ΩøÁî®ÁöÑÁî®Êà∑‰ø°ÊÅØ:', userInfo)
					
					// 2. Ë∞ÉÁî®ÂêéÁ´ØAPIËøõË°åÁôªÂΩï
					const res = await authAPI.wechatLogin(loginRes.code, userInfo)
					
					console.log('ÁôªÂΩïAPIËøîÂõû:', res)
					
					// ÁôªÂΩïÊàêÂäüÂêéÔºåÁõ¥Êé•‰ΩøÁî®ËøîÂõûÁöÑÁî®Êà∑‰ø°ÊÅØ
					if (res && res.user) {
						this.user = res.user
						// ‰ºòÂÖà‰ΩøÁî®ÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØÔºàÂç≥‰ΩøÊòµÁß∞ÊòØ"ÂæÆ‰ø°Áî®Êà∑"‰πüÊòØÊúâÊïàÁöÑÔºåËøôÊòØÊ®°ÊãüÂô®ÁöÑÈªòËÆ§ÂÄºÔºâ
						// Âú®ÁúüÊú∫‰∏ä‰ºöÊòæÁ§∫ÁúüÂÆûÁöÑÂæÆ‰ø°ÊòµÁß∞
						if (userInfo && (userInfo.nickName || userInfo.avatarUrl)) {
							this.userInfo = userInfo
							console.log('‰ΩøÁî®ÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØÔºåËÆæÁΩÆuserInfo:', this.userInfo)
						} else if (res.user.name) {
							// Âê¶Âàô‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑÁî®Êà∑Âêç
							this.userInfo = {
								nickName: res.user.name
							}
							console.log('‰ΩøÁî®ÂêéÁ´ØÁî®Êà∑ÂêçÔºåËÆæÁΩÆuserInfo:', this.userInfo)
						} else {
							this.userInfo = {
								nickName: 'ÂæÆ‰ø°Áî®Êà∑'
							}
							console.log('‰ΩøÁî®ÈªòËÆ§Áî®Êà∑ÂêçÔºåËÆæÁΩÆuserInfo:', this.userInfo)
						}
						console.log('ÁôªÂΩïÂêéÊúÄÁªàuserInfo:', this.userInfo)
						console.log('ÁôªÂΩïÂêéÊúÄÁªàuser:', this.user)
					} else {
						// Â¶ÇÊûúÊ≤°ÊúâËøîÂõûÁî®Êà∑‰ø°ÊÅØÔºåÂàôÈáçÊñ∞Ëé∑Âèñ
						await this.checkLogin()
					}
					
					uni.showToast({
						title: 'ÁôªÂΩïÊàêÂäü',
						icon: 'success'
					})
				} catch (err) {
					console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•Ôºö', err)
					uni.showToast({
						title: err.message || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
						icon: 'none',
						duration: 2000
					})
				} finally {
					this.logging = false
				}
			},
			
			async checkLogin() {
				try {
					const res = await authAPI.getMe()
					console.log('getMeÂìçÂ∫î:', res)
					if (res && res.user) {
						this.user = res.user
						// ‰ºòÂÖà‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØÔºàÂåÖÂê´ÊòµÁß∞ÂíåÂ§¥ÂÉèÔºâ
						const storedUserInfo = uni.getStorageSync('wechatUserInfo')
						if (storedUserInfo && (storedUserInfo.nickName || storedUserInfo.avatarUrl)) {
							this.userInfo = storedUserInfo
						} else {
							// Â¶ÇÊûúÊ≤°ÊúâÊú¨Âú∞Â≠òÂÇ®ÁöÑ‰ø°ÊÅØÔºå‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑÁî®Êà∑Âêç
							this.userInfo = {
								nickName: this.user.name || 'ÂæÆ‰ø°Áî®Êà∑'
							}
						}
					} else {
						this.user = null
						this.userInfo = {}
					}
				} catch (err) {
					console.error('Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂ§±Ë¥•:', err)
					this.user = null
					this.userInfo = {}
				}
			},
			
			
			async handleLogout() {
				uni.showModal({
					title: 'Á°ÆËÆ§ÈÄÄÂá∫',
					content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
					success: async (res) => {
						if (res.confirm) {
							try {
								await authAPI.logout()
								// Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®
								uni.removeStorageSync('wechatUserInfo')
								this.user = null
								this.userInfo = {}
								uni.showToast({
									title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
									icon: 'success'
								})
							} catch (err) {
								// Âç≥‰ΩøAPIË∞ÉÁî®Â§±Ë¥•Ôºå‰πüÊ∏ÖÈô§Êú¨Âú∞Áä∂ÊÄÅ
								this.user = null
								this.userInfo = {}
								uni.showToast({
									title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
									icon: 'success'
								})
							}
						}
					}
				})
			},
			
			goToWorkspace() {
				uni.navigateTo({
					url: '/pages/workspace/workspace'
				})
			},
			
			goToPortfolio() {
				uni.navigateTo({
					url: '/pages/portfolio/portfolio'
				})
			},
			
			goToOrders() {
